import React, { useEffect, useState } from 'react'
import axios from 'axios'
import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  CardText,
  Row,
  Col
} from 'reactstrap'
import Chart from 'react-apexcharts'
import {Star} from "react-feather";

const SupportTracker = props => {
  const [data, setData] = useState(null)

  useEffect(() => {
    axios.get('/card/card-analytics/support-tracker').then(res => setData(res.data))
  }, [])

  const options = {
      plotOptions: {
        radialBar: {
          size: 150,
          offsetY: 20,
          startAngle: -150,
          endAngle: 150,
          hollow: {
            size: '65%'
          },
          track: {
            background: '#fff',
            strokeWidth: '100%'
          },
          dataLabels: {
            name: {
              offsetY: -5,
              fontFamily: 'Montserrat',
              fontSize: '1rem'
            },
            value: {
              offsetY: 15,
              fontFamily: 'Montserrat',
              fontSize: '1.714rem'
            }
          }
        }
      },
      colors: [props.danger],
      fill: {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          type: 'horizontal',
          shadeIntensity: 0.5,
          gradientToColors: [props.primary],
          inverseColors: true,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [0, 100]
        }
      },
      stroke: {
        dashArray: 8
      },
      labels: ['Средняя оценка']
    }
  const series = [props.appraisal.percent]

  return data !== null ? (
    <Card>
      <CardHeader className='pb-0'>
        <CardTitle tag='h4'>{data.title}</CardTitle>
      </CardHeader>
      <CardBody>
        <Row>
          <Col sm='2' xs={12} className='d-flex flex-column flex-wrap text-center'>
            <h1 className='font-large-2 font-weight-bolder mt-2 mb-0'>{props.counter}</h1>
            <CardText>отзывов</CardText>
          </Col>
          <Col sm='2' xs={12} className='d-flex flex-column flex-wrap text-center'>
            <h1 className='font-large-2 font-weight-bolder mt-2 mb-0'>{props.counter * 5}</h1>
            <CardText>оценок</CardText>
          </Col>
          <Col sm='2' xs={12} className='d-flex flex-column flex-wrap text-center'>
            <h1 className='font-large-2 font-weight-bolder mt-2 mb-0'>{props.appraisal.cntStars}</h1>
            <CardText>звезд</CardText>
          </Col>
          <Col sm='6' xs={12} className='d-flex justify-content-center'>
            <Chart options={options} series={series} type='radialBar' height={270} id='support-tracker-card' />
          </Col>
        </Row>
        <div className='d-flex justify-content-around mt-1'>
          <div className='text-center'>
            <CardText className='mb-50'>5 <Star size={20} color={props.warning} /></CardText>
            <span className='font-large-1'>{props.appraisal.five}</span>
          </div>
          <div className='text-center'>
            <CardText className='mb-50'>4 <Star size={20} color={props.warning} /></CardText>
            <span className='font-large-1'>{props.appraisal.four}</span>
          </div>
          <div className='text-center'>
            <CardText className='mb-50'>3 <Star size={20} color={props.warning} /></CardText>
            <span className='font-large-1'>{props.appraisal.three}</span>
          </div>
          <div className='text-center'>
            <CardText className='mb-50'>2 <Star size={20} color={props.warning} /></CardText>
            <span className='font-large-1'>{props.appraisal.two}</span>
          </div>
          <div className='text-center'>
            <CardText className='mb-50'>1 <Star size={20} color={props.warning} /></CardText>
            <span className='font-large-1'>{props.appraisal.one}</span>
          </div>
        </div>
      </CardBody>
    </Card>
  ) : null
}
export default SupportTracker
