import Chart from 'react-apexcharts'
import { Card, CardTitle, CardText, CardBody, Row, Col } from 'reactstrap'
import React from "react";

const Earnings = ({ diplom, info, warning, danger }) => {
  const options = {
    chart: {
      toolbar: {
        show: false
      }
    },
    dataLabels: {
      enabled: false
    },
    legend: { show: true },
    comparedResult: [2, -3, 8],
    labels: ['3 степень', '2 степень', '1 степень'],
    stroke: { width: 0 },
    colors: [info, warning, danger],
    grid: {
      padding: {
        right: -20,
        bottom: -8,
        left: -20
      }
    },
    plotOptions: {
      pie: {
        startAngle: -10,
        donut: {
          labels: {
            show: true,
            name: {
              offsetY: 15
            },
            value: {
              offsetY: -15,
              formatter(val) {
                return `${parseInt(val)}`
              }
            },
            total: {
              show: true,
              offsetY: 15,
              label: 'всего',
              formatter(w) {
                return diplom.cntDiploms
              }
            }
          }
        }
      }
    },
    responsive: [
      {
        breakpoint: 1325,
        options: {
          chart: {
            height: 100
          }
        }
      },
      {
        breakpoint: 1200,
        options: {
          chart: {
            height: 120
          }
        }
      },
      {
        breakpoint: 1065,
        options: {
          chart: {
            height: 100
          }
        }
      },
      {
        breakpoint: 992,
        options: {
          chart: {
            height: 120
          }
        }
      }
    ]
  }

  return (
    <Card className='earnings-card'>
      <CardBody>
        <CardTitle className='mb-1'>Дипломы</CardTitle>
        <Chart options={options} series={[diplom.third, diplom.second, diplom.first]} type='donut' height={120} />
      </CardBody>
    </Card>
  )
}

export default Earnings
