import classnames from 'classnames'
import Avatar from '@components/avatar'
import {TrendingUp, User, Box, DollarSign, FileText, Image, Clock, Codesandbox, Sliders, UserCheck, Star} from 'react-feather'
import { Card, CardHeader, CardTitle, CardBody, CardText, Row, Col, Media } from 'reactstrap'
import React from "react";

const StatsCard = ({ cols, review, warning }) => {
  const data = [
    {
      title: review.information,
      subtitle: 'Информация',
      color: 'light-primary',
      icon: <FileText size={24} />
    },
    {
      title: review.design,
      subtitle: 'Дизайн',
      color: 'light-info',
      icon: <Image size={24} />
    },
    {
      title: review.speed,
      subtitle: 'Скорость',
      color: 'light-danger',
      icon: <Clock size={24} />
    },
    {
      title: review.system,
      subtitle: 'Система',
      color: 'light-success',
      icon: <Codesandbox size={24} />
    },
    {
      title: review.structure,
      subtitle: 'Структура',
      color: 'light-secondary',
      icon: <Sliders size={24} />
    },
    {
      title: `Да: ${review.university.yes}%`,
      titleSecond: `Нет: ${review.university.no}%`,
      subtitle: 'Поступление',
      color: 'light-warning',
      icon: <UserCheck size={24} />
    }
  ]

  const renderData = () => {
    return data.map((item, index) => {
      const margin = Object.keys(cols)
      return (
        <Col
          key={index}
          {...cols}
          className={classnames({
            [`mb-2 mb-${margin[0]}-0`]: index !== data.length - 1
          })}
        >
          <Media>
            <Avatar color={item.color} icon={item.icon} className='mr-2' />
            <Media className='my-auto' body>
              <h4 className='font-weight-bolder mb-0'>
                {item.titleSecond ?
                    <> {item.title} <br/> {item.titleSecond} </> :
                    <> {item.title} <Star size={15} color={warning} /> </>
                }
              </h4>
              <CardText className='font-small-3 mb-0'>{item.subtitle}</CardText>
            </Media>
          </Media>
        </Col>
      )
    })
  }

  return (
    <Card className='card-statistics'>
      <CardHeader>
        <CardTitle tag='h4'>Статистика отзывов</CardTitle>
        <CardText className='card-text font-small-2 mr-25 mb-0'>Обновлено только что</CardText>
      </CardHeader>
      <CardBody className='statistics-body'>
        <Row>{renderData()}</Row>
      </CardBody>
    </Card>
  )
}

export default StatsCard
