import React, {useContext, useEffect} from 'react';
import {ThemeColors} from '@src/utility/context/ThemeColors';
import Sales from '@src/views/ui-elements/cards/analytics/Sales';
import SupportTracker from '@src/views/ui-elements/cards/analytics/SupportTracker';
import {Row, Col} from 'reactstrap';
import OrdersReceived from '@src/views/ui-elements/cards/statistics/OrdersReceived';
import SubscribersGained from '@src/views/ui-elements/cards/statistics/SubscribersGained';
import '@styles/react/libs/charts/apex-charts.scss';
import StatsCard from "../../ui-elements/cards/statistics/StatsCard";
import Earnings from "../../ui-elements/cards/analytics/Earnings";
import GoalOverview from "../../ui-elements/cards/analytics/GoalOverview";
import {connect} from "react-redux";
import {getAllStat} from "../../../redux/reducers/stat-reducer";

const AnalyticsDashboard = (props) => {
    useEffect(() => {
        props.getAllStat();
    }, []);
    const {colors} = useContext(ThemeColors);
    return (
        <div id='dashboard-analytics'>
            {props.allStat && <>
                <Row className='match-height'>
                    <Col lg='6' sm='6'>
                        <SubscribersGained counter={`${props.allStat.counter.cntReg}`}/>
                    </Col>
                    <Col lg='6' sm='6'>
                        <OrdersReceived counter={`${props.allStat.counter.cntTest}`} warning={colors.warning.main}/>
                    </Col>
                </Row>
                <Row className='match-height'>
                    <Col lg='4' md='6' xs='12'>
                        <Sales primary={colors.primary.main} review={props.allStat.review}/>
                    </Col>
                    <Col lg='8' md='6' xs='12'>
                        <SupportTracker
                            counter={props.allStat.counter.cntReviews}
                            appraisal={props.allStat.appraisal}
                            warning={colors.warning.main}
                            primary={colors.primary.main}
                            danger={colors.danger.main}/>
                    </Col>
                </Row>
                <Row>
                    <Col xl='12'>
                        <StatsCard warning={colors.warning.main} review={props.allStat.review} cols={{lg: 2, md: 4, xs: 12}}/>
                    </Col>
                </Row>
                <Row>
                    <Col lg='4' md='6' xs='12'>
                        <Earnings diplom={props.allStat.diplom} info={colors.info.main} warning={colors.warning.main} danger={colors.danger.main}/>
                    </Col>
                    <Col lg='8' md='6' xs='12'>
                        <GoalOverview creative={props.allStat.creative} success={colors.success.main}/>
                    </Col>
                </Row>
            </>}
        </div>
    )
}

const mapStateToProps = (state) => ({
    allStat: state.statPage.allStat
});

export default connect(mapStateToProps, {getAllStat})(AnalyticsDashboard);
